/* General Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f9;
  color: #333;
}

.App {
  text-align: center;
  padding: 20px;
  max-width: 900px;
  margin: 0 auto;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
}

/* Header */
.header {
  background-color: #282c34;
  color: #ffffff;
  padding: 20px;
}

.header h1 {
  margin: 0;
  font-size: 3em;
  color: #61dafb; /* Brighter, appealing color */
}

.header p {
  font-size: 1.2em;
  margin-top: 10px;
  color: #aaaaaa; /* Subtle gray for the subtitle */
}

/* Sections */
.about-me, .skills, .projects, .contact {
  margin-top: 40px;
  padding: 20px;
}

.about-me h2, .skills h2, .projects h2, .contact h2 {
  color: #333;
  font-size: 2em;
  margin-bottom: 20px;
  border-bottom: 2px solid #61dafb;
  display: inline-block;
  padding-bottom: 5px;
}

.about-me p, .skills ul, .projects p, .contact p {
  font-size: 1.1em;
  color: #666;
  line-height: 1.6;
}

/* Skills */
.skills ul {
  list-style-type: none;
  padding-left: 0;
  margin-top: 20px;
}

.skills li {
  background-color: #e0e0e0;
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  display: inline-block;
  color: #444;
}

/* Projects */
.project-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.project {
  background-color: #f7f7f7;
  padding: 20px;
  width: 45%;
  margin: 10px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}

.project h3 {
  font-size: 1.5em;
  color: #333;
}

.project a {
  color: #61dafb;
  text-decoration: none;
  font-weight: bold;
}

.project a:hover {
  color: #0056b3;
}

/* Contact */
.contact a {
  color: #0073b1;
  text-decoration: none;
}

.contact a:hover {
  color: #005999;
}

/* Footer */
footer {
  margin-top: 40px;
  padding: 20px;
  background-color: #282c34;
  color: white;
}
